<template lang="html">
  <div class="group-view has-header">
     <user-bar holder="登录参与讨论"></user-bar>
     <group title="租房找室友" :items="group_a">
       <a class="list-link" href="#" slot="more">更多相关小组</a>
     </group>
     <group title="聊聊人生" :items="group_b">
       <a class="list-link" href="#" slot="more">更多相关小组</a>
     </group>
     <download-app></download-app>
  </div>
</template>

<script>
import { mapState } from 'vuex'

import UserBar from '../components/UserBar'
import group from '../components/group'
import DownloadApp from '../components/DownloadApp'
export default {
  components: {
    UserBar,
    group,
    DownloadApp
  },
  computed: {
    ...mapState({
      group_a: state => state.group.group_a,
      group_b: state => state.group.group_b,
      group_c: state => state.group.group_c
    })
  },
  created: function () {
    this.$store.dispatch('getGroup')
  }
}
</script>

<style lang="scss" scoped>
.list-link {
  display: block;
  margin-bottom: 3rem;
  padding: 1.5rem 0;
  font-size: 1.6rem;
  line-height: 1.8rem;
  text-align: center;
  color: #42bd56;
}

.user-bar {
  margin-bottom: 1.5rem;
}
</style>



